h1 { font-size: 54px; }

h4 { font-size: 21px; }

h6 { font-family: "Helvetica Neue L", Arial, sans-serif; font-size: 13px; }

body { font-family: "Helvetica Neue", Arial, sans-serif; font-family: Arial, sans-serif; font-size: 14px; line-height: 1.4; color: #75858f; min-height: 100%; }

a { text-decoration: none; color: #000; }

.container { min-width: 1440px; min-height: 100%; }

.out { min-height: 100%; }

.green { color: #4cddac; }

.pink { color: #f84a75; }

.grey { color: #ccc; }

.grey-dark { color: #75858f; }

.logo { width: 92px; }
.logo img { width: 100%; }

.comparison { display: flex; flex-direction: row; align-items: center; }
.comparison__text { margin: 0 12px; }

.content-title { font-family: "Helvetica Neue L", Arial, sans-serif; padding: 10px 0; color: #75858f; }

.events { margin: 20px; }
.events__el { display: inline-block; }

.img-box { margin-left: 8px; width: 36px; height: 36px; background-image: url("../img/img-desktop.jpg"); background-size: cover; }

.nav { display: flex; flex-direction: row; }
.nav__text { padding: 2px 14px; border-radius: 16px; text-align: center; font-size: 14px; letter-spacing: 0.4px; cursor: pointer; user-select: none; opacity: 0.5; }
.nav__text:hover { background-color: #e9e8e8; cursor: pointer; }
.nav .is-active { opacity: 1; background-color: #e9e8e8; }


